<script>
import TableComponent from "@/components/Table/Table.vue";
import PaginationComponent from "@/components/Pagination1/Pagination1.vue";
export default {
  name: "ticketInformationComponent",
  components: { TableComponent, PaginationComponent },
  data() {
    return {
      tableColumns: [
        { id: "index", label: "項次", width: "70" },
        { id: "fab", label: "違規廠商", width: "" },
        { id: "violation_Date", label: "違規日期", width: "" },
        { id: "end_Time", label: "結案日期", width: "" },
        { id: "fineSum", label: "罰款金額", width: "" },
        { id: "appeal", label: "結案狀態", width: "" },
        { id: "", label: "備註", width: "" },
      ],
      data: [],
      s_Id: "",
      s_Name: "",
      tableList: [],
    };
  },
  computed: {
    tableData() {
      const data = this.data;
      data.forEach((e, index) => (e.index = index + 1));
      return data;
    },
  },
  methods: {
    getFineDetail(s_Id) {
      this.$store
        .dispatch("QryFineDetail", s_Id)
        .then((res) => {
          const data = res.data.data;
          this.data = JSON.parse(JSON.stringify(data));
          this.changePage(1);
        })
        .catch((err) => console.log(err));
    },
    goBack() {
      this.$router.go(-1);
    },
    changePage(page) {
      this.$store.state.currentPage = page;
      const cur = (page - 1) * 10;
      const last = page * 10;
      this.tableList = this.tableData.slice(cur, last);
    },
    printerHandler(item) {
      window.open(`/#/ticketInformationPrint/${item.fine_Id}`);
    },
  },
  mounted() {
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;
    this.s_Id = this.$route.params.s_Id;
    this.s_Name = this.$route.params.s_Name;
    this.getFineDetail(this.s_Id);
  },
};
</script>

<template src="./ticketInformation.html"></template>
